.hove {
    position: relative;
}

@media (min-width:990px) {
    .hove:hover::after {
        content: '';
        position: absolute;
        bottom: 0;
        width: 85%;
        left: 9px;
        background: linear-gradient(to left, white, #ffb607, #ffb607) white;
        height: 2px;
        transition: all 500ms ease-in-out;
        animation: left;
        animation-duration: 500ms;
        animation-timing-function: ease-in-out;
        animation-iteration-count: once;
    }
}

@keyframes left {
    from {
        width: 0%;
    }
    to {
        width: 85%;
    }
}

.headermain::before {
    bottom: 0;
    /* right: 0; */
    content: "";
    height: 1.5px;
    width: 85%;
    position: absolute;
    /* background: rgba(0, 0, 0, 0) linear-gradient(to right, rgba(0, 0, 0, 0), #e4cb50, #c2a134); */
    background: linear-gradient(to left, rgba(0, 0, 0, 0), #ffb607, #ffb607) rgba(0, 0, 0, 0);
}

